myApp.config(["$stateProvider", "$urlRouterProvider", "$locationProvider", function($stateProvider, $urlRouterProvider, $locationProvider) {
    $locationProvider.html5Mode(false);
    // $locationProvider.hashPrefix()
    $urlRouterProvider.otherwise('/home');

    var indexInIndex = { templateUrl: 'index.html' },
        headerInhome = { templateUrl: 'tpls/header.html' },
        footerInhome = { templateUrl: 'tpls/footer.html' };
    /*首页*/
    $stateProvider
        .state('index', {
            abstract: true,
            views: {
                'header@index': headerInhome,
                'footer@index': footerInhome,
                'content@index': {
                    templateUrl: 'tpls/main.html'
                }
            }
        })
        /*home*/

    .state('index.home', {
            url: '/home',
            'content@index': {
                templateUrl: 'tpls/main.html'
            }
        })
        /*search*/
        .state('index.search', {
            url: '/search?:keywords:catid',
            views: {
                'content@index': {
                    templateUrl: 'tpls/search.html'
                }
            }
        })
        /*category*/
        .state('index.category', {
            url: '/category',
            views: {
                'content@index': {
                    templateUrl: 'tpls/category.html'
                }
            }
        })
        /*usercenter*/
        .state('index.usercenter', {
            url: '/usercenter',
            views: {
                'content@index': {
                    templateUrl: 'tpls/usercenter.html'
                }
            }
        })
        /*post*/
        .state('index.post', {
            url: '/post',
            views: {
                'content@index': {
                    templateUrl: 'tpls/post.html'
                }
            }
        })
        /*city*/
        .state('index.city', {
            url: '/city',
            views: {
                'content@index': {
                    templateUrl: 'tpls/city.html'
                }
            }
        })
        /*info*/
        .state('index.info', {
            url: '/info?infoid',
            views: {
                'content@index': {
                    templateUrl: 'tpls/info.html'
                }
            }
        })
        /*login*/
        .state('index.login', {
            url: '/login',
            views: {
                'content@index': {
                    templateUrl: 'tpls/login.html'
                }
            }
        })
        /*register*/
        .state('index.register', {
            url: '/register',
            views: {
                'content@index': {
                    templateUrl: 'tpls/reg.html'
                }
            }
        })
        /*report*/
        .state('index.report', {
            url: '/report?infoid',
            views: {
                'content@index': {
                    templateUrl: 'tpls/report.html'
                }
            }
        });
}]);
